import { Component, OnInit, Output, EventEmitter, ElementRef, Renderer2, OnDestroy} from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { FormControl } from '@angular/forms';


@Component({
    selector: 'search-bar',
    templateUrl: './search-bar.component.html',
    styleUrls: ['./search-bar.component.less']
})
export class SearchBarComponent implements OnInit, OnDestroy {

    @Output() search = new EventEmitter();
    searchTerm: BehaviorSubject<string> = new BehaviorSubject('');    
    searchControl = new FormControl();
    constructor(private el: ElementRef, private render: Renderer2) {

        // add search-bar class
        this.render.addClass(this.el.nativeElement, 'search-bar');
    }

    ngOnInit() {
        // Set initial value
        this.searchControl.setValue('', {emitEvent: false});
        
        this.searchControl.valueChanges.subscribe((res) => {
            this.search.emit(res);
        });
    }

    ngOnDestroy() {
        this.searchTerm.unsubscribe();
    }

    public reset(): void {
        this.searchControl.setValue('', {emitEvent: false});
    }



}
