import { Pipe, PipeTransform } from '@angular/core';
import { BaseResource } from '@app/core';

@Pipe({
  name: 'filter',
  pure: false
})
export class FilterPipe implements PipeTransform {

    transform<T extends BaseResource | string | number | {}>(rawArray: T[], searchTerm: string): T[] {

        if (searchTerm && typeof(searchTerm) === 'string') {
            const filtered: T[] = rawArray.filter((x: T): boolean => {

                const s: string = JSON.stringify(x);

                return s.toLowerCase().includes(searchTerm.toLowerCase());

            });

            return filtered;

        } else {
            return rawArray;
        }
    }

}
