import { OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { CustomerService, ProjectService, SupplyService, SupplierService, UpholsteryService } from '@app/core';

import { MatSnackBar, MatDialogRef } from '@angular/material';
import { FormGroup, FormBuilder, FormArray, AbstractControl, Validators, FormControl } from '@angular/forms';
import {
	Customer,
	BaseResource,
	Project,
	Supplier,
	Supply,
	Address,
	Upholstery,
} from '@app/core/models';
import { SafeUrl } from '@angular/platform-browser';
import { NumberValidators } from '@app/shared/validators';


export type ValidationMessage = {type: string, message: string};
export type ValidationMessages = {[key: string]: ValidationMessage[]};


export abstract class AddOrderComponent<O extends BaseResource>
	implements OnInit {
	public abstract form: FormGroup;
	public validation_messages: ValidationMessages = {
		name: [{ type: 'required', message: 'Customer name is required' }],
		delivery_date: [
			{ type: 'required', message: 'Delivery date is required' },
		],
		lead_time: [{ type: 'required', message: 'A Lead Time is required' }],
		currency: [{ type: 'required', message: 'A Currency is required' }],
		vat: [
			{ type: 'required', message: 'VAT is required' },
			{
				type: 'min',
				message: 'A value greater than or equal to 0 is required',
			},
        ],
        discount: [
            { type: 'required', message: 'Discount is required' },
            {
                type: 'min',
                message: 'A value greater than or equal to 0 is required',
            },
        ],
		items: [
			{ type: 'minLength', message: 'At least one item is required' },
		],
		quantity: [
			{ type: 'required', message: 'A quantity is required' },
			{ type: 'min', message: 'A value greater than 0 is required' },
		],
		price: [
			{ type: 'required', message: 'A price is required' },
			{ type: 'min', message: 'A value greater than 0 is required' },
		],
		dimension: [
			{
				type: 'integer',
				message: 'A valid positive integer is required',
			},
			{ type: 'min', message: 'A value greater than 0 is required' },
		],
	};

	constructor(
		public order: O,
		public contact: Customer | Supplier,
		public contactService: CustomerService | SupplierService,
		public itemResource: Upholstery | Supply,
		public itemService: UpholsteryService | SupplyService,
		public project: Project,
		public projectService: ProjectService,
		protected fb: FormBuilder,
		protected snackBar: MatSnackBar,
		protected dialogRef: MatDialogRef<AddOrderComponent<O>>
	) {}

	ngOnInit() {}

	public get addresses(): FormArray {
		return this.form.get('customer.addresses') as FormArray;
	}

	public get items(): FormArray {
		return this.form.get('items') as FormArray;
	}

	public getValue(
		ctrlName: string,
		grp: FormGroup = this.form
	): string | number {
		const ctrl: AbstractControl | null = grp.get(ctrlName);

		if (['comments', 'remarks'].includes(ctrlName)) {
			return ctrl ? (ctrl.value || '').split(/\n/g) : [];
		}

		return ctrl ? ctrl.value || '' : '';
	}

	public getDimensionText(grp: FormGroup): string {
		let i: string;
		let ctrl;
		let s: string = '';

		for (i of ['width', 'depth', 'height']) {
			ctrl = grp.get(i);

			s += ctrl
				? ctrl.value
					? [i, ctrl.value + ' '].join(': ')
					: ''
				: '';
		}

		return s;
	}

	public displayError(val: string | FormControl, t: string): boolean {
		if (typeof val === 'string') {
			const f: AbstractControl | null = this.form.get(val);

			return f === null ? false : f.invalid && f.hasError(t);
		} else if (val instanceof FormControl) {
			return val.invalid && val.hasError(t);
		} else {
			return false;
		}
	}

	public getErrorMessage<
		K extends keyof AddOrderComponent<O>['validation_messages']
	>(k: K, t: string): string {
		const validationMessages: ValidationMessage[] = this
			.validation_messages[k];
		const msg: ValidationMessage = validationMessages.find(
			(val: ValidationMessage) => val.type === t
		) || { type: '', message: '' };
		return msg.message;
	}

	public setCustomer(val: Customer | string): void {
		let c: Customer;

		if (typeof val === 'string') {
			c = new Customer(this.contactService as CustomerService);
			c.name = val;
		} else {
			c = val as Customer;
		}

		const custControl = this.form.get('customer') || false;

		if (custControl) {
			custControl.patchValue(c);
			this.addresses.controls[0].patchValue(
				c.addresses[0] || new Address()
			);
		}

		this.form.get('currency').patchValue(c.currency);
	}

	public setProject(project: Project | string): void {
		const p =
			typeof project === 'string'
				? {
						id: null as null,
						codename: project,
				  }
				: project;
		this.getFormGroup('project').patchValue(p);
	}

	public addItem(val: Upholstery | Supply | string): void {
		let obj: FormGroup;
		obj =
			typeof val === 'string'
				? this.addCustomItem(val)
				: this.addStandardItem(val);
		this.items.push(obj);
	}

	public updateItemImage(index: number, files: FileList): void {
		this.uploadImage(files).subscribe((res) => {
			this.items.controls[index].get('image').patchValue(res);
			this.snackBar.open('Image Uploaded', 'Dimiss', { duration: 3000 });
		});
	}

	public removeItem(index: number): void {
		this.items.removeAt(index);
	}

	public getItemImageURL(item: FormGroup): string | SafeUrl {
		const itemObj = item.value;

		return itemObj
			? itemObj.schematic
				? itemObj.schematic.url || ''
				: itemObj.image
					? itemObj.image.url || ''
					: ''
			: '';
	}

	protected addStandardItem(r: Upholstery | Supply): FormGroup {
		return r instanceof Upholstery
			? this._addStandardProduct(r)
			: this._addStandardSupply(r);
	}

	protected _addStandardProduct(r: Upholstery): FormGroup {
		const fg = this.fb.group({
			id: [r.id, Validators.required],
			description: [
				{ value: r.description, disabled: r.id },
				Validators.required,
			],
			model: this.fb.group({
				id: [{ value: r.model.id, disabled: true }],
				model: [{ value: r.model.model, disabled: true }],
				name: [{ value: r.model.name, disabled: true }],
			}),
			configuration: this.fb.group({
				id: [{ value: r.configuration.id, disabled: true }],
				configuration: [
					{ value: r.configuration.configuration, disabled: true },
				],
			}),
			unit_price: [r.price, [Validators.required, Validators.min(1)]],
			quantity: [null, [Validators.required, Validators.min(1)]],
			width: [r.width, [NumberValidators.integer, Validators.min(0)]],
			depth: [r.depth, [NumberValidators.integer, Validators.min(0)]],
			height: [r.height, [NumberValidators.integer, Validators.min(0)]],
			comments: [''],
			image: this.fb.group({
				id: [(r.image || { id: '' }).id],
				url: [(r.image || { url: '' }).url],
			}),
			schematic: this.fb.group({
				id: [(r.schematic || { id: '' }).id],
				url: [(r.schematic || { url: '' }).url],
			}),
		});

		return fg;
	}

	protected _addStandardSupply(r: Supply): FormGroup {
		const fg = this.fb.group({
			id: [r.id, Validators.required],
			description: [r.description, Validators.required],
			reference: [r.reference],
			type: [r.type],
			cost: [r.cost, [Validators.required, Validators.min(1)]],
			quantity: [null, [Validators.required, Validators.min(1)]],
			comments: [null],
			image: this.fb.group({
				id: [(r.image || { id: '' }).id],
				url: [(r.image || { url: '' }).url],
			}),
		});

		return fg;
	}

	protected addCustomItem(description: string): FormGroup {
		const fg = this.fb.group({
			description: [description, Validators.required],
			unit_price: [0, [Validators.required, Validators.min(1)]],
			quantity: [0, [Validators.required, Validators.min(1)]],
			width: [0, [NumberValidators.integer, Validators.min(0)]],
			depth: [0, [NumberValidators.integer, Validators.min(0)]],
			height: [0, [NumberValidators.integer, Validators.min(0)]],
			image: this.fb.group({
				id: [null],
				url: [''],
			}),
			comments: [''],
		});

		return fg;
	}

	protected getFormControl(controlName: string): AbstractControl {
		return this.form.get(controlName) || this.addFormGroup(controlName);
	}

	protected addFormControl(controlName: string): AbstractControl {
		const control: FormControl = this.fb.control(null);
		return this.form.registerControl(controlName, control);
	}

	protected getFormGroup(groupName: string): AbstractControl {
		return this.form.get(groupName) || this.addFormGroup(groupName);
	}

	protected addFormGroup(groupName: string): AbstractControl {
		const group: FormGroup = this.fb.group({});
		return this.form.registerControl(groupName, group);
	}

	protected uploadImage(files: FileList): Observable<{}> {
		this.snackBar.open('Uploading Image...', 'Dismiss');
		const file = files[0];

		return this.order.upload(file, '/api/v1/acknowledgement/file/');
	}
}



