import { BaseResource } from '@app/core';

/**
 * @enumerable decorator that sets the enumerable property of a class field to false.
 * @param value true|false
 */
export function enumerable<T extends BaseResource>(value: boolean): (target: T, propertyKey: string) => void {
    function f(target: T, propertyKey: string): void {
        const descriptor = Object.getOwnPropertyDescriptor(target, propertyKey) || {};
        if (descriptor.enumerable !== value && value) {
            descriptor.enumerable = value;
            Object.defineProperty(target, propertyKey, descriptor);

        }
    }

    return f;
}

export function nonenumerable<T extends BaseResource>(target: T, propertyKey: string): void {

    const descriptor = Object.getOwnPropertyDescriptor(target, propertyKey) || {};
    descriptor.enumerable = false;
    Object.defineProperty(target, propertyKey, descriptor);

}
