
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

interface CurrentUserInterface {
    permissions: string[];
    modules: string[];
    groups: string[];
} 


@Injectable({
	providedIn: 'root',
})
export class AuthService {
    user: CurrentUserInterface;
	groups: string[] = [];
	permissions: string[] = [];
	modules: string[] = [];
	auth_token: string | null = null;

	constructor(protected http: HttpClient) {
		this.http
			.get('api/v1/current_user')
			.subscribe((res: CurrentUserInterface) => {
                this.user = res;
				this.groups = res['groups'];
				this.modules = res['modules'];
				this.permissions = res['permissions'];
			});
	}

	public get isLoggedIn(): boolean {
		return this.user ? true : false;
	}

	public hasRole(role: string): boolean {
        if (!this.user) {
            return false;
        }

		return this.user.groups.includes(role.toLowerCase());
	}

	public canAccessModule(module: string): boolean {
        if (!this.user) {
			return false;
        }
        
		return this.user.modules.includes(module.toLowerCase());
	}
}
