import { Directive, ElementRef, HostListener, Input, HostBinding, Renderer2 } from '@angular/core';


@Directive({
  selector: '[drag]'
})
export class OnDragDirective {
    

    private _dragging: boolean = false;
    private _ghost?: Node;

    @HostBinding('class.dragging')
    get dragging() { return this._dragging; }


    @Input() 
    drag!: string | number | {[index: string]: string | number | {}};

    constructor(private el: ElementRef<HTMLElement>, private renderer: Renderer2) {
        this.el.nativeElement.draggable = true;
    }

    @HostListener('dragstart', ['$event'])
    onDragstart($event: DragEvent) {
        // Clear old ghost elements
        this._remove_ghost_elements();

        // Turn on dragging flag
        this._dragging = true;

        // Setting Ghost Image
        this._ghost = this.el.nativeElement.cloneNode(true);
        this.renderer.setStyle(this._ghost, 'width', this.el.nativeElement.offsetWidth + 'px');
        this.renderer.addClass(this._ghost, 'ghost');
        this.renderer.appendChild(document.body, this._ghost);

        if ($event.dataTransfer) {
            $event.dataTransfer.setDragImage(this._ghost as HTMLElement, 
                                             this._getGhostX($event.offsetX), 
                                             this._getGhostY($event.offsetY));

            // Set Data
            $event.dataTransfer.setData('text/plain', JSON.stringify(this.drag));
        }
        
    }

    @HostListener('dragend', ['$event'])
    onDragend() {
        // Turn on dragging flag
        this._dragging = false;

        this._remove_ghost_elements();
    }

    @HostListener('drop', ['$event'])
    onDrop() {
        // Turn on dragging flag
        this._dragging = false;

        this._remove_ghost_elements();
    }

    private _remove_ghost_elements(): void {
        if (this._ghost) {
            this.renderer.removeChild(document.body, this._ghost);
            this._ghost = undefined;
        }
        
        const list: HTMLCollectionOf<Element> = document.body.getElementsByClassName('ghost');
        Array.prototype.forEach.call(list, (el: Element) => el.remove());
    }

    private  _getGhostX(offsetX: number) {
        return offsetX; // - this.el.nativeElement.offsetWidth;
    }

    private  _getGhostY(offsetY: number) {
        return offsetY; // - this.el.nativeElement.offsetHeight;
    }
}
