import { Injectable } from '@angular/core';
import { BaseResource } from './base';
import { Customer } from './customer';
import { Project } from './project';
import { Room } from './room';
import { MediaFile } from './file';
import { ShippingService } from '../services';
import { Item as AItem } from './acknowledgement';

interface ItemInterface {
    id?: number;
    description: string;
    quantity: number;
    comments?: string;
    image?: MediaFile;
    item?: AItem;
}

export class Item implements ItemInterface {
    id?: number;
    description!: string;
    quantity: number;
    item?: AItem;
    comments?: string;
}


export interface ShippingInterface {
    id?: number | null;
    customer?: Customer;
    acknowledgement?: {};
    project?: Project;
    room?: Room;
    items: Item[];
    comments?: string;
    pdf: MediaFile;
    delivery_date?: string | Date;
    employee?: {[key: string]: string | number};

    // Readonly
    time_created?: string | Date;
    last_modified?: string | Date;
}

@Injectable()
export class Shipping extends BaseResource implements ShippingInterface {

    customer?: Customer;
    acknowledgement?: {};
    project?: Project;
    room?: Room;
    items: Item[];
    comments?: string;
    pdf: MediaFile;
    delivery_date?: string | Date;
    employee?: {[key: string]: string | number};

    // Readonly
    time_created?: string | Date;
    last_modified?: string | Date;

    constructor(protected service: ShippingService) {
        super(service);
    }

    public get displayText(): string {
        return this.id ? this.customer ? this.id + ': ' + this.customer.name : String(this.id) : '';
    }
}
