import { Pipe, PipeTransform } from '@angular/core';
import { BaseResource } from '@app/core';

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {

    transform<T extends BaseResource & {status: string}>(rawArray: T[], status: string): T[] {
        // Filters objects with status property,
        // and then filters objects with matching status
        const arr = rawArray.filter((x: T) => x.hasOwnProperty('status'))
                            .filter((x: T) => x.status ? x.status.toLowerCase() === status.toLowerCase() : false);

        console.log(arr);

        return arr;
    }

}
