import { Injectable } from '@angular/core';
import { BaseResource } from './base';
import { AcknowledgementService } from '../services';
import { Fabric } from './fabric';
import { Customer } from './customer';
import { Project } from './project';
import { Room } from './room';
import { Phase } from './phase';
import { MediaFile } from './file';

export interface ItemInterface {
    id?: number;
    description: string;
    product?: number;
    unit_price: number;
    comments?: string;
    width?: number;
    depth?: number;
    height?: number;
    units?: string;
    fabric?: Fabric;
    image?: MediaFile;
    pillows?: Array<{}>;
    components?: Array<{}>;

    total?: number;

    [index: string]: number | string | Fabric | MediaFile | Array<{}> | undefined;

}

export class Item implements ItemInterface {
    id?: number;
    description!: string;
    type?: string;
    quantity?: number;
    product?: number;
    unit_price: number = 0;
    comments?: string;
    width?: number;
    depth?: number;
    height?: number;
    units?: string;
    fabric?: Fabric;
    fabric_quantity?: number;
    image?: MediaFile;
    pillows?: Array<{}>;
    components?: Array<{}>;

    total?: number;

    [index: string]: number | string | Fabric | Array<{}> | MediaFile | undefined;

}

export interface AcknowledgementInterface {
    id?: number | null;
    customer?: Customer;
    company?: string;
    project?: Project;
    room?: Room;
    phase?: Phase;
    items: Item[];
    remarks?: string;
    po_id?: string;
    shipping_method?: string;
    fob?: string;
    delivery_date?: string | Date;
    vat: number;
    status?: string;
    discount?: number;
    second_discount?: number;
    files?: MediaFile[];

    // Readonly
    total?: number;
    subtotal?: number;
    time_created?: string | Date;
    last_modified?: string | Date;
    employee?: {[key: string]: string | number};
}

@Injectable()
export class Acknowledgement extends BaseResource implements AcknowledgementInterface {
    customer?: Customer;
    company?: string;
    project?: Project;
    room?: Room;
    phase?: Phase;
    items: Item[] = [];
    remarks?: string;
    po_id?: string;
    shipping_method?: string;
    fob?: string;
    delivery_date?: string | Date;
    vat: number = 7;
    status?: string;
    discount?: number;
    second_discount?: number;
    files?: Array<{[key: string]: string | number}>;

    // Readonly
    total?: number;
    subtotal?: number;
    time_created?: string | Date;
    last_modified?: string | Date;
    employee?: {[key: string]: string | number};

    constructor(protected service: AcknowledgementService) {
        super(service);
    }

    public get displayText(): string {
        return this.id ? this.customer ? this.id + ': ' + this.customer.name : String(this.id) : '';
    }
}
