import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';

export interface BaseDataInterface {
    id: number;
    status?: number;
    [index: string]: string | number | BaseDataInterface;
}

@Injectable()
export class BaseService {
    protected baseURL: string = 'api/v1/';

    constructor(protected http: HttpClient) {
    }

    public query(userParams: {[index: string]: string | string[]} = {}): Observable<BaseDataInterface[]> {
        // Creates a new HttpParam object regardless
        // if there is any user params

        let params = new HttpParams();
        let i;
        let v: string | string[];
        let h;

        // Iterate over params
        for (i in userParams) { // tslint: disable-prefer-const
            // Get value
            v = userParams[i];

            // If value is array then add to param via 
            // append. If a string, then set regularly.
            if (v instanceof Array) {
                for (h of v) { /* tslint: disable: prefer-const */
                    params = params.append(i + '[]', h);
                }
            } else if (typeof v === 'string') {
                params = params.set(i, v);
            }
        }

        // Sets up the get call and returns an observable
        return this.http.get<BaseDataInterface[]>(this.baseURL, {params});
    }

    public get(id: number): Observable<BaseDataInterface> {

        let url: string = this.baseURL;
        url += id + '/';
        return this.http.get<BaseDataInterface>(url);
    }

    public post(data: {}): Observable<BaseDataInterface> {
        return this.http.post<BaseDataInterface>(this.baseURL, data);
    }

    public put(data: {id: number}): Observable<BaseDataInterface> {
        let url: string = this.baseURL;
        const objId: number = data.id;

        // Check that the id is valid
        if (!objId) {
            let msg: string = 'Property "id" for this "PUT" call is invalid because it is: ';
            msg += data.id;
            throw TypeError(msg);
        }

        // Assemble the URL
        url += objId + '/';

        return this.http.put<BaseDataInterface>(url, data);
    }

    public upload(file: File, url: string): Observable<BaseDataInterface> {
        const formData: FormData = new FormData();
        formData.append('file', file, file.name);
        return this.http.post<BaseDataInterface>(url, formData);
    }
}
