import { BaseResource } from './base';

export interface AddressInterface {
    id?: number;
    address1: string | null;
    city: string | null;
    territory: string | null;
    country: string | null;
    zipcode: string | null;
}

export class Address implements AddressInterface {
    address1: string | null = null;
    city: string | null = null;
    territory: string | null = null;
    country: string | null = null;
    zipcode: string | null = null;
}

export interface ContactInterface {
    name?: string;
    name_th?: string;
    telephone?: string;
    fax?: string;
    email?: string;
    is_supplier: boolean;
    is_customer: boolean;
    discount: number;
    currency: string;
    notes?: string;
    deleted?: boolean;
    last_modified?: string | Date;
    website?: string;
    tax_id?: string;
    bank?: string;
    bank_account_number?: string;
    trcloud_id?: number;

}

export abstract class Contact extends BaseResource {
    name?: string;
    name_th?: string;
    telephone?: string;
    fax?: string;
    email?: string;
    is_supplier: boolean = false;
    is_customer: boolean = false;
    discount: number = 0;
    currency: string = 'THB';
    notes?: string;
    deleted?: boolean = false;
    last_modified?: string | Date;
    website?: string;
    tax_id?: string;
    bank?: string;
    bank_account_number?: string;
    trcloud_id?: number;

    addresses: AddressInterface[] = [new Address()];

    public get displayText(): string {
        return this.name || '';
    }
}




