import { Injectable } from '@angular/core';

import { BaseResource } from './base';
import { PurchaseOrderService } from '../services';
import { Supply } from './supply';
import { Phase } from './phase';
import { Project } from './project';
import { Room } from './room';
import { Acknowledgement } from './acknowledgement';
import { Supplier } from './supplier';


export interface Item {
    id?: number;
    description: string;
    cost: number;
    comments: string;
    units: string;
    status: string;
    image?: {id: number, url: string};
    supply?: Supply;
    type?: string;
    reference?: string;
}

@Injectable()
export class PurchaseOrder extends BaseResource {
    company: string = 'Alinea Group';
    supplier?: Supplier;
    discount?: number = 0;
    second_discount?: number;
    vat: number = 7;
    terms?: string;
    currency: string = 'THB';
    comments?: string;
    items: Item[] = [];
    status: string = 'open';
    total?: number;

    // Acknowledgment attributes
    acknowledgement?: Acknowledgement;

    // Project attributes
    room?: Room;
    project?: Project;
    phase?: Phase;

    constructor(protected service: PurchaseOrderService) {
        super(service);
    }
}
