import { Injectable, OnChanges } from '@angular/core';
import { Observable ,  Subscription } from 'rxjs';
import { map, take } from 'rxjs/operators';

import { BaseService, BaseDataInterface } from '../services';
import { enumerable } from '@app/shared/decorators/enumerable';
import { MediaFile } from './file';

// type RemoveFunctionType<C> = Pick<C, {[K in keyof C]: C[K] extends () => void ? never : K}[keyof C]>;

interface BaseResourceInterface {
    id: number | null;
    description?: string;
    image?: MediaFile;

    // Date Keys List now but find way to make detection auto
    delivery_date?: string | Date;
    time_created?: string | Date;
    last_modified?: string | Date;
    receive_date?: string | Date;

    query(param: {}): Observable<this[]>;
    save(success?: (res: this) => void, error?: ({}) => void): Subscription;
    get(id: number): Observable<this>;
    upload(file: File, url: string): Observable<BaseDataInterface>;
}

// type DateKeys<T> = { [Key in keyof T]: T[Key] extends Date ? Key : never }[keyof T];


@Injectable()
export class BaseResource implements OnChanges, BaseResourceInterface {

    // Instance properties

    public id: number|null = null;
    public description?: string;
    public image?: MediaFile;

    public delivery_date?: string | Date;
    public time_created?: string | Date;
    public last_modified?: string | Date;
    public receive_date?: string | Date;

    @enumerable(false)
    protected date_keys: string[] = [
        'delivery_date',
        'time_created',
        'last_modified',
        'receive_date'
    ];

    constructor(protected service: BaseService) {
        if (service instanceof BaseService) {
            Object.defineProperty(this, 'service', {
                value: service,
                enumerable: false,
                writable: true,
                configurable: true                
            });
        }
    }

    ngOnChanges() {

    }

    @enumerable(false)
    get displayText(): string {
        return this.description || '';
    }

    @enumerable(false)
    get imageURL(): string {
        return this.image ? this.image.url || '' : '';
    }

    @enumerable(false)
    public query<T extends this>(params?: {}): Observable<T[]> {
        return this.service.query(params)
                           .pipe(map(
                                (data: BaseDataInterface[]): T[] => data.map(
                                    (d: BaseDataInterface): T => this.createResource(d)
                                )
                            ));
    }

    @enumerable(false)
    public get<T extends this>(id: number): Observable<T> {
        return this.service.get(id)
                           .pipe(map((data: BaseDataInterface): T => this.createResource(data)));
    }

    @enumerable(false)
    public save(success?: (res: this) => void, error?: ({}) => void): Subscription {
        return (this.id ? this.update() : this.create())
               .pipe(map((data) => Object.assign(this, data)))
               .pipe(take(1))
               .subscribe(success, error);
    }

    @enumerable(false)
    public upload(file: File, url: string): Observable<BaseDataInterface> {
        return this.service.upload(file, url).pipe(take(1));
    }

    @enumerable(false)
    protected create<T extends this>(): Observable<T> {

        const data = {};
        Object.assign(data, this);
      
        return this.service.post(data)
                           .pipe(map((r: BaseDataInterface) => this.createResource(r)));
    }

    @enumerable(false)
    protected update<T extends this>(): Observable<T> {

        // Test for ID. ID is required to update
        if (this.id && typeof this.id === 'number') {
            let data = {id: this.id};
            Object.assign(data, this);
            const props = Object.getOwnPropertyDescriptors(this);
            data = Object.defineProperties(data, props);

            return this.service.put(data)
                               .pipe(map((r: BaseDataInterface) => this.createResource(r)));
        } else {
            throw TypeError('Valid number for property "id" is required to update this resource');
        }
        
    }

    @enumerable(false)
    protected createResource<T extends this, K extends keyof U, U extends T & {[key: string]: Date | string}>(data: BaseDataInterface): T {
        const resource: U = Object.create(Object.getPrototypeOf(this),  {
            service: {
                value: this.service,
                writable: true,
                enumerable: false
            }
        }) as U;

        Object.assign(resource, data);

        /**
         * Change all timestamps to dates
         */
        let i: K;
        let re_str = '^(-?(?:[1-9][0-9]*)?[0-9]{4})-(1[0-2]';
        re_str += '|0[1-9])-(3[01]|0[1-9]|[12][0-9])T(2[0-3]|[01][0-9]):';
        re_str += '([0-5][0-9]):([0-5][0-9])(.[0-9]+)?(Z)?$';

        const re = new RegExp(re_str);
        const keys = Object.getOwnPropertyNames(resource) as K[];
        
        for (i of keys) {
            if (typeof(resource[i]) === 'string' ? re.test(resource[i] as string) : false) {
                const rawDate: string = String(resource[i]);
                const v: U[K] | Date = new Date(rawDate);
                resource[i] = v;
                Object.defineProperty(resource, i, {value: new Date(v)});
            }
                
        }

        return resource;
    }
}
