import { Component, OnInit, Input, ViewChild, Output, EventEmitter } from '@angular/core';
import { BaseService, BaseResource } from '@app/core';
import { SearchListComponent } from '../search-list/search-list.component';


@Component({
    selector: 'add-item',
    templateUrl: './add-item.component.html',
    styleUrls: ['./add-item.component.less']
})
export class AddItemComponent implements OnInit {
    @ViewChild('search')
    search: SearchListComponent | undefined;
    @Input() placeholder: string = 'Add an Item';
    @Input() service: BaseService | undefined;
    @Input() resource: BaseResource | undefined;
    @Input() params: {[index: string]: string} = {};

    @Output() add: EventEmitter<BaseResource | string> = new EventEmitter();

    constructor() {}

    ngOnInit() {

    }

    public get value(): BaseResource | string {
        return this.search ? this.search.value : '';
    }

    public addItem(): void {

        this.add.emit(this.value);

        if (this.search) { this.search.reset(); }

    }

    public onSelect(): void {

    }
}
