import { Injectable } from '@angular/core';

import { BaseResource } from './base';
import { QuotationService } from '../services';
import { Fabric } from './fabric';
import { Acknowledgement } from './acknowledgement';
import { Project } from './project';
import { Customer, CustomerInterface } from './customer';
import { MediaFile } from './file';
import { Upholstery } from './upholstery';


interface ItemInterface {
    id?: number;
    description: string;
    type?: string;
    product?: number;
    unit_price?: number;
    quantity: number;
    comments?: string;
    width?: number;
    depth?: number;
    height?: number;
    units?: string;
    fabric?: Fabric;
    fabric_quantity?: number;
    image?: MediaFile;
    pillows?: Array<{}>;
    components?: Array<{}>;

    total?: number;

    [index: string]: number | string | Fabric | Array<{}> | undefined | MediaFile;
}

export class Item implements ItemInterface {
    id?: number;
    product?: number;
    description!: string;
    unit_price: number = 0;
    quantity: number = 1;
    comments?: string;
    width?: number;
    depth?: number;
    height?: number;
    units?: string;
    fabric?: Fabric;
    fabric_quantity?: number;
    image?: MediaFile;
    pillows?: Array<{}>;
    components?: Array<{}>;

    total?: number; 

    [index: string]: number | string | Fabric | Array<{}> | undefined | MediaFile;

    constructor() {    
    }
    
}

interface QuotationInterface {
    company?: string;
    customer: Customer | CustomerInterface;
    project?: Project;
    items: Array<Item | ItemInterface| Upholstery>;
    remarks?: string;
    po_id?: string;
    status: string;
    shipping_method?: string;
    fob?: string;
    delivery_date?: string | Date;
    lead_time: string;
    currency: string;
    
    files?: MediaFile[];
    acknowledgement?: Acknowledgement;

    // Vats
    vat: number;
    vat_amount?: number;
    
    // Discounts
    discount?: number;
    discount_amount?: number;
    second_discount?: number;
    second_discount_amount?: number;

    


    // Readonly
    // Totals
    subtotal?: number;
    post_discount_total?: number;
    total?: number;
    grand_total?: number;

    time_created?: string | Date;
    employee?: {[key: string]: string | number};
}

@Injectable()
export class Quotation extends BaseResource implements QuotationInterface {
    customer: Customer;
    company: string = 'Alinea Group';
    project?: Project;
    items: Array<Item | Upholstery> = [];
    remarks?: string;
    po_id?: string;
    status: string = 'open';
    shipping_method?: string;
    fob?: string;
    delivery_date?: string | Date = new Date();
    lead_time: string = '4 Weeks';
    currency: string = 'THB';
    vat: number = 7;
    discount: number = 0;
    second_discount: number = 0;
    files?: MediaFile[];

    acknowledgement?: Acknowledgement;

    // Readonly
    total?: number;
    subtotal?: number;
    time_created?: string | Date;
    employee?: {[key: string]: string | number};

    constructor(protected service: QuotationService) {
        super(service);
    }
}
