import { Injectable } from '@angular/core';

import { UserService } from '../services';
import { BaseResource } from './base';

@Injectable()
export class User extends BaseResource {
    username!: string;
    web_ui_version?: string;
    email!: string;
    name!: string;

    groups: string[];
    permissions: string[];
    modules: string[];

    constructor(protected service: UserService) {
        super(service);
    }
}
