import { Component, OnInit, Renderer2, ElementRef, AfterViewInit, EventEmitter, Output, Input, OnDestroy } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BehaviorSubject ,  Observable } from 'rxjs';
import { MatAutocompleteSelectedEvent } from '@angular/material';
import { BaseService } from '@app/core';
import { BaseResource } from '@app/core/models';
import { distinctUntilChanged ,  debounceTime, take } from 'rxjs/operators';


@Component({
  selector: 'search-list',
  templateUrl: './search-list.component.html',
  styleUrls: ['./search-list.component.less']
})
export class SearchListComponent implements OnInit, AfterViewInit, OnDestroy {
    private _items: BehaviorSubject<BaseResource[]> = new BehaviorSubject<BaseResource[]>([]);
    public items: Observable<BaseResource[]> = this._items.asObservable();
    public _selectedItem!: BaseResource;

    @Input()
    public placeholder!: string;
    @Input()
    public service!: BaseService;
    @Input()
    public resource?: BaseResource;
    @Input()
    public description!: string;
    @Input()
    public avatar: boolean = false;
    @Input()
    protected params: {} = {};
    @Output()
    public selected: EventEmitter<BaseResource> = new EventEmitter();
    @Output()
    public blurred: EventEmitter<string> = new EventEmitter();

    autoControl = new FormControl();

    constructor(private renderer: Renderer2,
                private el: ElementRef) {

        // Set Validators for the control if required
        // is present. Set here so that they are immediately 
        // registered
        if (this.el.nativeElement.hasAttribute('required')) {
            this.autoControl.setValidators([Validators.required, Validators.minLength(1)]);
        }
    }

    ngOnInit() {
        let currentList: BaseResource[];
        // Set initial value
        this.autoControl.setValue(this.resource ? this.resource.id ? this.resource : null : null, {emitEvent: false});

        // Set the placeholer if not already set
        this.placeholder = this.placeholder ? this.placeholder : this.resource ? this.resource.constructor.name : '';

        this.autoControl.valueChanges.pipe(
            debounceTime(500),
            distinctUntilChanged()
        ).subscribe((val: string | null) => {
            currentList = this._items.getValue();

            const qParams: {[index: string]: string | Array<number | null>} = {q: val || '', exclude: currentList.map((x) => x.id)};
            
            // Filter Valid params
            Object.entries(this.params).filter((x) => x[1]).forEach(() => {
                // qParams[x[0]] = x[1];
            });
            
            if (this.resource) {
                this.resource.query(qParams).pipe(take(1)).subscribe((results: BaseResource[]) => {
                    const cList: BaseResource[] = this._items.getValue();
                    const idList: Array<number | null> = cList.map((x) => x.id);
    
                    const newList: BaseResource[] = cList.concat(results.filter((x: BaseResource): boolean => {
                        return !idList.includes(x.id) ? true : false;
                    }));
    
                    this._items.next(newList);
    
                });
            }
            
            this._items.next(currentList);
        });
    }

    ngAfterViewInit() {
        if (this.el.nativeElement.hasAttribute('required')) {
            const input = this.el.nativeElement.getElementsByTagName('input')[0];
            this.renderer.setAttribute(input, 'required', 'true');

            const placeholder = this.el.nativeElement.getElementsByTagName('label')[0];
            this.renderer.appendChild(placeholder, this.renderer.createText('*'));
        }
    }

    ngOnDestroy() {
        this._items.unsubscribe();
    }

    public get value(): BaseResource | string {
        return this.autoControl.value;
    }

    public onBlur(): void {
        if (typeof(this.autoControl.value) === 'string') {
            this.blurred.emit(this.autoControl.value);
        }
    }

    public getDescription(res?: BaseResource): string | undefined {
        return res ? res.displayText : undefined;
    }

    public getErrorMessage(): string {
        return this.resource ? this.resource.constructor.name + ' is required' : '';
    }

    public itemSelected<T extends BaseResource>(evt: MatAutocompleteSelectedEvent): void {
        this._selectedItem = evt.option.value;

        this.selected.emit(this._selectedItem as T);
    }

    public reset(): void {
        this.autoControl.setValue(null, {emitEvent: false});
    }


}
