import { Injectable } from '@angular/core';

import { CustomerService } from '@app/core/services/customer.service';
import { ContactInterface, Contact } from './contact';


export interface CustomerInterface extends ContactInterface {
    first_name?: string;
    last_name?: string;
    contacts?: Array<{}>;
}

@Injectable()
export class Customer extends Contact implements CustomerInterface {
    contacts?: Array<{}>;
    is_customer: boolean = true;
    is_supplier: boolean = false;

    constructor(protected service: CustomerService) {
        super(service);
    }
}
