import { Injectable } from '@angular/core';

import { BaseResource } from './base';
import { SupplierService } from '../services';

@Injectable()
export class Supplier extends BaseResource {
    bank?: string;
    bank_account_number?: string;
    currency: string = 'THB';
    deleted: boolean = false;
    discount?: number;
    email?: string;
    fax?: string;
    first_name?: string;
    is_customer?: boolean = false;
    is_supplier: boolean = true;
    last_modified?: string;
    last_name?: string;
    name!: string;
    name_th?: string;
    tax_id?: string;
    telephone?: string;
    trcloud_id?: number;
    website?: string;

    addresses: Array<{}> = [];

    constructor(protected service: SupplierService) {
        super(service);
    }

    public get displayText(): string {
        return this.name;
    }
}
