import { Injectable } from '@angular/core';

import { SupplyService } from '../services';
import { MediaFile } from './file';
import { Supplier } from './supplier';
import { BaseResource } from './base';

@Injectable()
export class Supply extends BaseResource {
    description!: string;
    cost?: number;
    reference?: string;
    image?: MediaFile;
    type?: string;
    supplier?: Supplier;
    suppliers?: Supplier[];

    constructor(protected service: SupplyService) {
        super(service);
    }

    public get displayText(): string {
        return this.id && this.description ? this.id + ' ' + this.description : '';
    }
}

