import { Injectable } from '@angular/core';

import { BaseResource } from './base';
import { UpholsteryService } from '../services';
import { Model } from './model';
import { Configuration } from './configuration';
import { enumerable } from '@app/shared/decorators/enumerable';
import { MediaFile } from './file';

@Injectable()
export class Upholstery extends BaseResource {
    description!: string;
    type?: string;
    collection?: string;
    model!: Model;
    configuration!: Configuration;
    width?: number;
    depth?: number;
    height?: number;
    price: number = 0;
    image?: MediaFile;
    schematic?: MediaFile;

    constructor(protected service: UpholsteryService) {
        super(service);
    }

    @enumerable(false)
    public get imageURL(): string {
        return this.schematic ? this.schematic.url || '' : this.image ? this.image.url || '' : '';
    }
}

